export default class Audio {
  constructor() {
    this.audioSwitch = document.querySelector("input");
    this.menumusic = document.getElementById("menuaudio");
    this.gamemusic = document.getElementById("gameaudio");
    this.playAudio = false;
  }
  update(game) {
    if (this.audioSwitch.checked) {
      this.playAudio = true;
      if (game.gameState === 0 || typeof game.gameState === "undefined") {
        this.menumusic.play();
        this.gamemusic.pause();
      } else {
        this.menumusic.pause();
        this.gamemusic.play();
      }
    } else {
      this.playAudio = false;
      this.menumusic.pause();
      this.gamemusic.pause();
    }
  }
  start() {
    this.gamemusic.currentTime = 0;
  }
  menuStart(game) {
    this.gamemusic.pause();
    this.menumusic.play();
    if (this.audioSwitch.checked) {
      this.menumusic.currentTime = 0;
      this.gamemusic.pause();
      this.menumusic.play();
    } else {
      this.playAudio = false;
      this.menumusic.pause();
      this.gamemusic.pause();
    }
  }
}
