export default class InputHandler {
  constructor(paddle, game) {
    this.canvas = document.getElementById("game");
    this.canvas.addEventListener("mousedown", mobileInput);
    this.canvas.addEventListener("touchstart", mobileInput);

    function mobileInput(event) {
      if (game.gameState < 1 || typeof game.gameState === "undefined") {
        game.start();
      }
      function move(evnt) {
        if (evnt.clientX + paddle.width < paddle.x) {
          paddle.moveLeft();
        }
        if (evnt.clientX > paddle.x + paddle.width) {
          paddle.moveRight();
        }
      }
      function touchMove(evnt) {
        if (evnt.touches[0].clientX + paddle.width < paddle.x) {
          paddle.moveLeft();
        }
        if (evnt.touches[0].clientX > paddle.x + paddle.width) {
          paddle.moveRight();
        }
      }
      document.addEventListener("mousemove", move);
      document.addEventListener("touchmove", touchMove);
      document.addEventListener("mouseup", (event) => {
        document.removeEventListener("mousemove", move);
      });
      document.addEventListener("touchend", (event) => {
        document.removeEventListener("touchmove", touchMove);
      });
    }
    document.addEventListener("keydown", (event) => {
      switch (event.keyCode) {
        case 37:
          paddle.moveLeft();
          break;

        case 39:
          paddle.moveRight();
          break;
        case 32:
          if (game.gameState < 1 || typeof game.gameState === "undefined") {
            game.start();
          }
          break;
      }
    });
  }
}
