import Paddle from "./paddle";
import InputHandler from "./input";
import Ball from "./ball";
import Brick from "./brick";
import Rain from "./rain";
import Tutorial from "./tutorial";
import Audio from "./audio";
import CollisionDetection from "./collision";

let startScreen = document.getElementById("startScreen");

export default class Game {
  constructor() {
    this.paddle = new Paddle(this);
    this.ball = new Ball(this);
    this.brick = new Brick();
    this.audio = new Audio(this);
    this.debrisArray = [];
    this.groundHeight = [];
    for (let x = 0; x < 800; x++) {
      this.updateGround(x, 600);
    }
    this.xploArray = [];
    this.score = 0;
    this.bestScore = 0;
    this.stage = 2;
    this.deaths = 0;
    this.debrisGenerated = 0;
    this.maxSpeed = 0;
    this.tutorial = new Tutorial(this);
    this.collisionDetection = new CollisionDetection();
    this.scoreDisplay = document.getElementById("score");

    this.tip = document.getElementById("deathTip");
    this.tipScore = document.getElementById("deathScore");
    this.tipBest = document.getElementById("deathBest");
    this.tipBestest = document.getElementById("deathBestest");
    this.deathTexts = document.querySelectorAll(".deathText");

    this.bestScoreDisplay = document.getElementById("best");
    this.newHighScore = 0;
    this.announcements = document.getElementById("announcements");
    this.dark = document.getElementById("darkness");
    this.rain = new Rain();
    this.canvas = document.getElementById("rain");
    this.rain.start(this.canvas, 33);
    this.textQueue = [];
    this.alreadyDisplaying = false;
    let gameState = 0;
    new InputHandler(this.paddle, this);
  }
  updateGround(x, height) {
    this.groundHeight[x] = height;
  }

  update(time) {
    this.audio.update(this);

    if (this.gameState > 0) {
      this.paddle.update(time);
      this.ball.update(this.paddle, this, time);
      this.brick.update(time);
      this.collisionDetection.update(this.ball, this.brick, this);
    }
    if (this.score + 4 >= 4 * this.stage) {
      this.stage += 1;
      this.displayAnnouncement("new set of enemy bricks approaching");
      this.brick.waitAndMoveBricks(this.stage);
      this.rain.start(this.canvas, 10);
    }
    this.game = this;
    this.debrisArray.forEach((debris) => {
      debris.update(this, time);
    });
    this.xploArray.forEach((xplo, index) => {
      xplo.update(this, index, time);
    });
  }

  draw(ctx) {
    this.brick.draw(ctx);
    this.debrisArray.forEach((debris) => {
      debris.draw(ctx);
    });
    this.xploArray.forEach((xplo, index) => {
      xplo.draw(ctx);
    });
    this.ball.draw(ctx);
    this.paddle.draw(ctx);
    this.scoreDisplay.textContent = this.score;
    if (this.score > this.bestScore) {
      this.bestScore = this.score;
      this.bestScoreDisplay.textContent = this.score;
      if (this.newHighScore === 0 && this.score > 5) {
        this.displayAnnouncement(
          `impressive officer!<br> you got a new high score <br> ðŸ”¥ðŸ”¥ðŸ”¥${this.score}ðŸ”¥ðŸ”¥ðŸ”¥ `
        );
      }
      this.newHighScore = 1;
    }
  }
  start() {
    this.rain.end();
    this.brick.end();
    startScreen.hidden = true;
    this.debrisArray = [];
    this.xploArray = [];
    this.score = 0;
    this.stage = 2;
    this.brick.start();
    this.ball.start();
    this.paddle.reset();
    this.gameState = 1;
    this.ball.ballTrail = [];
    this.groundHeight = [];
    this.newHighScore = 0;
    this.audio.start();
    this.dark.style.opacity = 1;
    this.deathTexts.forEach((elment) => {
      elment.hidden = true;
    });
    this.moveDeathTextstoPos();
    this.fadeDarkness(true);
    this.ball.slomo = true;
    setTimeout(() => {
      this.ball.slomo = false;
    }, 3000);

    for (let x = 0; x < 800; x++) {
      let height = 600;
      this.groundHeight[x] = height;
    }
    this.canvas = document.getElementById("rain");
    this.rain.start(this.canvas, 5);

    let doTutorial = function (game) {
      game.displayAnnouncement(game.tutorial.getTutorialText());
    };
    doTutorial(this);
  }
  displayAnnouncement(text) {
    if (!this.alreadyDisplaying) {
      this.alreadyDisplaying = true;
      this.announcements.innerHTML = `<h2>${text}</h2>`;
      this.announcements.style.opacity = 1;
      setTimeout(() => {
        this.fadeAnnouncement();
        this.alreadyDisplaying = false;
      }, 4000);
    } else {
      this.textQueue.push(text);
      setTimeout(() => {
        this.displayAnnouncement(this.textQueue.pop());
      }, 100);
    }
  }
  fadeAnnouncement() {
    setTimeout(() => {
      if (this.announcements.style.opacity > 0) {
        this.announcements.style.opacity -= 0.05;
        this.fadeAnnouncement();
      }
    }, 50);
  }
  fadeDarkness(dir) {
    setTimeout(() => {
      if (dir) {
        this.dark.style.opacity -= 0.01;
        if (this.dark.style.opacity > 0.2) {
          this.fadeDarkness(true);
        }
      }
    }, 40);
  }
  fadeInDarkness(dir) {
    setTimeout(() => {
      if (dir) {
        this.dark.style.opacity = parseFloat(this.dark.style.opacity) + 0.01;
        if (this.dark.style.opacity <= 1) {
          this.fadeInDarkness(true);
        }
      }
    }, 40);
  }
  moveDeathTextsAway() {
    this.deathTexts.forEach((elment, index) => {
      elment.style.transform = `translate(${-1000 * (index + 1)}px,0px)`;
    });
  }
  moveDeathTextstoPos() {
    this.deathTexts.forEach((elment, index) => {
      elment.style.transform = `translate(2000px,0px)`;
    });
  }
  moveDeathTextsIn() {
    this.deathTexts.forEach((elment, index) => {
      elment.style.transform = `translate(0px,0px)`;
    });
  }

  endScreen() {
    this.deaths++;
    this.fadeInDarkness(true);
    this.moveDeathTextsIn();
    this.tutorial.updateData(this.getInfo());
    startScreen.hidden = true;
    this.tip.innerHTML = this.tutorial.getSpecialText();
    this.tipScore.innerHTML = this.score;
    this.tipBest.innerHTML = this.bestScore;
    this.deathTexts.forEach((elment) => {
      elment.hidden = false;
    });
    if (this.score === this.bestScore) {
      this.tipBestest.innerHTML =
        "ðŸ”¥ðŸ”¥which is your bestest score hell yes motherfucker!!ðŸ”¥ðŸ”¥";
    } else {
      this.tipBestest.hidden = true;
    }

    this.ball.reset();
    setTimeout(() => {
      if (this.gameState !== 1) {
        this.moveDeathTextsAway();
        this.fadeDarkness();
      }
    }, 13500);
    setTimeout(() => {
      if (this.gameState !== 1) {
        startScreen.hidden = false;
        this.fadeInDarkness();
      }
    }, 15000);
  }
  getInfo() {
    let data = {
      bestScore: this.bestScore,
      maxSpeed: this.maxSpeed,
      deaths: this.deaths,
      debrisGenerated: this.debrisGenerated,
      stage: this.stage
    };

    return data;
  }
}
