export default class Paddle {
  constructor(game) {
    this.width = 100;
    this.height = 20;
    this.maxSpeed = 350;
    this.xStart = 400 - this.width / 2;
    this.x = this.xStart;
    this.speed = 0;
    this.color = "#FFA500";

    this.initialAcc = 125;
  }
  update(dt) {
    dt = dt / 4;
    this.x += (this.speed * dt) / 100;
    if (this.speed > 0) {
      this.speed -= (1 * dt) / 2;
    } else if (this.speed < 0) {
      this.speed += (1 * dt) / 2;
    }
    if (this.x <= 0) {
      this.x = 0;
    }
    if (this.x > 700) {
      this.x = 700;
    }
    if (this.speed > this.maxSpeed) {
      this.speed = this.maxSpeed;
    }
    if (this.speed < -this.maxSpeed) {
      this.speed = -this.maxSpeed;
    }
  }

  draw(ctx) {
    let gradient = ctx.createLinearGradient(0, 800, 0, 0);
    gradient.addColorStop(0, "rgba(12, 12, 23, 1)");
    gradient.addColorStop(0.235, "rgba(12, 12, 23, 1)");
    gradient.addColorStop(0.285, "rgba(265,165,0,1)");
    gradient.addColorStop(1, "rgba(12, 12, 23, 1)");
    ctx.fillStyle = gradient;

    ctx.fillRect(this.x, 580, this.width, this.height);
  }
  moveLeft() {
    this.speed -= this.initialAcc;
  }
  moveRight() {
    this.speed += this.initialAcc;
  }
  reset() {
    this.x = this.xStart;
    this.speed = 0;
  }
  stop() {
    this.speed = 0;
  }
}
