export default class Ball {
  constructor(game) {
    this.startX = 50;
    this.startX2 = 750;
    this.startY = 100;
    this.startXSpeed = 130;
    this.startYSpeed = 0;
    this.acceleration = 25;
    this.wallBounce = 6;
    this.graceWidth = 10;
    this.ballTrail = [];
    this.slomo = false;
    this.firstHit = 0;
    this.startPos = Math.round(Math.random());
    this.x = this.startPos > 0 ? this.startX2 : this.startX;
    this.y = this.startY;

    this.radius = 8;
    this.yReserve = 0;
    this.color = "rgba(255,165,0,1.000)";

    this.xSpeed = this.startPos > 0 ? -this.startXSpeed : this.startXSpeed;
    this.ySpeed = this.startYSpeed;
  }
  update(paddle, game, dt) {
    let fps = 1000 / dt;
    dt = dt / 5;
    let timeSample = fps > 240 ? 4 : fps > 180 ? 3 : fps > 120 ? 2 : 1;
    if (!this.slomo) {
      if (this.firstHit > 0) {
        this.yReserve = this.firstHit / timeSample;
        this.firstHit = 0;
      }
      this.x += (this.xSpeed * dt) / 200;
      this.y += (this.ySpeed * dt) / 200;
      this.ySpeed += 1 * dt;
    } else {
      this.x += (this.xSpeed * dt) / (200 * timeSample * dt);
      this.y += (this.ySpeed * dt) / (200 * timeSample * dt);
      this.ySpeed += ((1 * dt) / 8 / timeSample) * dt;
      this.yReserve += ((1 * dt) / 8) * (dt / 4);
    }
    let randomLight = (Math.abs(this.ySpeed) + Math.abs(this.xSpeed)) / 10;
    this.color = `rgba(${255 - randomLight},${165 - randomLight},0,1)`;

    if (
      this.y > 585 &&
      this.x > paddle.x - this.graceWidth &&
      this.x < paddle.x + (paddle.width + this.graceWidth)
    ) {
      this.ySpeed = -(this.ySpeed + this.acceleration);
      this.xSpeed += paddle.speed + Math.random() * 50;
      if (this.yReserve > 0) {
        this.ySpeed -= this.yReserve * timeSample;
        this.yReserve = 0;
      }
    }
    if (this.y < 0) {
      this.ySpeed = -(this.ySpeed - this.acceleration);
    }
    if (this.x > 800) {
      this.xSpeed = -this.xSpeed;
      this.x -= this.wallBounce;
    }
    if (this.x < 0) {
      this.xSpeed = -this.xSpeed;
      this.x += this.wallBounce;
    }
    if (this.y > 600) {
      if (game.gameState > 0) {
        game.endScreen();
      }
      game.gameState = 0;
      game.audio.menuStart(game);
    }
    this.ballTrail.push([this.x, this.y, this.color]);
    let speed = (Math.abs(this.ySpeed) + Math.abs(this.xSpeed)) / dt;
    if (game.maxSpeed < speed) {
      game.maxSpeed = Math.round(speed);
      if (this.deaths > 0)
        game.displayAnnouncement(`wow, new max speed ${game.maxSpeed}!`);
    }
  }
  draw(ctx) {
    ctx.beginPath();
    ctx.strokeStyle = this.color;
    ctx.fillStyle = this.color;
    ctx.lineWidth = 1;
    ctx.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
    ctx.stroke();
    ctx.fill();
    ctx.closePath();
    this.ballTrail.reverse();
    for (let x = 1; x < this.ballTrail.length; x++) {
      ctx.strokeStyle = this.ballTrail[x][2];
      ctx.lineWidth = x < 5 ? 15 : 15 * (75 / (75 + x * 4));
      ctx.beginPath();
      ctx.moveTo(this.ballTrail[x - 1][0], this.ballTrail[x - 1][1]);
      ctx.lineTo(this.ballTrail[x][0], this.ballTrail[x][1]);
      ctx.stroke();
      ctx.closePath();
    }
    if (this.ballTrail.length > 150) {
      this.ballTrail.reverse();
      this.ballTrail.shift();
    }
  }
  reset() {
    this.ballTrail = [];
    this.x = 1000;
  }
  start() {
    this.x = this.startX;
    this.y = this.startY;
    this.xSpeed = this.startXSpeed;
    this.ySpeed = this.startYSpeed;
  }
}
