import Debris from "./debris";
import Xplosion from "./xplosion";
export default class CollisionDetection {
  update(ball, bricks, game) {
    if (game.gameState > 0) {
      bricks.brickArray.forEach((brick) => {
        if (brick[0] > 0) {
          if (
            ball.x + ball.radius * 2 >= brick[1] &&
            ball.x <= brick[1] + bricks.sizeX &&
            ball.y + ball.radius * 2 >= brick[2] &&
            ball.y <= brick[2] + bricks.sizeY
          ) {
            brick[0] -= 1;
            if (brick[0] === 0) {
              game.score += 1;
              for (let x = 0; x < 10; x++) {
                game.xploArray.push(
                  new Xplosion(
                    brick[1] + Math.random() * 20 - 10,
                    brick[2] + Math.random() * 20 - 10,
                    x * 10
                  )
                );
              }
            }

            ball.ySpeed = -(ball.ySpeed / 1.2);
            ball.xSpeed = -(ball.xSpeed / 1.5) + (Math.random() * 500 - 250);
            ball.y += 4;
            ball.ySpeed += 20;
            let pieces = 5 + Math.random() * 12;
            while (pieces > 0) {
              game.debrisGenerated++;
              game.debrisArray.push(
                new Debris(
                  Math.random() * 40 - 20 + brick[1],
                  Math.random() * 20 - 10 + brick[2],
                  [brick[0]],
                  game
                )
              );
              pieces--;
            }
          }
        }
      });
    }
  }
}
