export default class Brick {
  constructor() {
    this.sizeX = 52;
    this.sizeY = 20;
    this.brickPerRow = 15;
    this.startCap = 0;
    this.brickArray = [];
    this.doMoveBricks = 0;
    this.moveToStage = 1;
    this.colors = `rgba(255,${0 + (this.moveToStage - 1) * 33},${
      0 + (this.moveToStage - 1) * 33
    },1)`;
  }
  update(time) {
    let dt = time / 4;
    if (this.doMoveBricks > 0) {
      this.doMoveBricks += 1 * dt;
    }
    if (this.doMoveBricks > 400) {
      this.doMoveBricks = 0;
      this.moveBricks(this.moveToStage);
    }
  }
  draw(ctx) {
    this.brickArray.forEach((brick) => {
      ctx.fillStyle = this.colors = `rgba(255,${
        165 + (brick[0] - 1) * 33
      },0,1)`;

      if (brick[0] > 0) {
        ctx.fillRect(brick[1], brick[2], this.sizeX, this.sizeY);
      }
    });
  }
  generateBrickArray(location, hp, caps) {
    let cap = this.startCap;
    let capY = location * 2;
    let count = 0;
    let other = 0;
    caps = typeof caps === "undefined" ? 0 : caps;
    for (let x = 0; x < this.brickPerRow; x++) {
      if (other > 0 || caps === 0) {
        this.brickArray.push([
          hp,
          cap + x * this.sizeX,
          capY + location * this.sizeY
        ]);

        if (count < this.brickPerRow - 1) {
          cap += 2;
          count++;
        }
        other--;
      } else {
        cap += 2;
        other++;
      }
    }
  }
  beginGen() {
    this.brickArray = [];
    let firstRow = 2;
    let secondRow = 2;
    for (let x = 0; x < firstRow; x++) {
      this.generateBrickArray(x, 2);
    }
    for (let x = 0; x < secondRow; x++) {
      this.generateBrickArray(firstRow + x, 1, 1);
    }
  }
  waitAndMoveBricks(stage) {
    this.doMoveBricks = 1;
    this.moveToStage = stage;
  }
  moveBricks(stage) {
    let newArray = [];
    let brickHp = Math.round(stage / 2) + 1;
    this.brickArray.forEach((brick) => {
      newArray.push([brick[0], brick[1], brick[2] + this.sizeY + 2]);
    });
    this.brickArray = newArray;

    this.generateBrickArray(0, brickHp);
  }
  start() {
    this.brickArray = [];
    this.doMoveBricks = 0;
    this.moveToStage = 2;
    this.beginGen();
  }
  end() {
    this.brickArray = [];
  }
}
