export default class Debris {
  constructor(x, y, color) {
    this.x = x;
    this.y = y;
    this.xSize = 15 + Math.random() * 25;
    this.ySize = 9 + Math.random() * 12;
    this.fallSpeed = 2;
    this.color = color;
    this.xDir = Math.random() * 4 - 2;
    this.debrisTrail = [];
    this.addTrail = 0;
    this.hitGround = 0;
  }
  update(game, dt) {
    dt = dt / 8;
    let height = 10000;
    for (
      let x = Math.round(this.x) + 1;
      x < this.x + Math.round(this.xSize) - 1;
      x++
    ) {
      if (game.groundHeight[x] < height) {
        height = game.groundHeight[x];
      }
    }
    if (
      this.y <= height - this.ySize ||
      this.x <= 0 ||
      this.x + this.xSize >= 800 ||
      this.y > 600
    ) {
      this.y += this.fallSpeed * dt;
      if (
        this.x + this.xDir * dt - this.xSize >= 0 ||
        this.x + this.xDir * dt + this.xSize <= 800
      ) {
        this.x += this.xDir * dt;
      } else {
        this.x -= this.xDir * dt;
      }

      this.xDir += this.xDir < 0 ? 0.008 : -0.008;
      let fps = 1000 / dt;
      dt = dt / 5;
      let timeSample = fps > 240 ? 4 : fps > 180 ? 3 : fps > 120 ? 2 : 1;
      if (this.addTrail > 3 + timeSample) {
        this.addTrail = 0;
        this.debrisTrail.push([
          this.x + this.xSize / 2,
          this.y + this.ySize / 2,
          this.color
        ]);
      } else {
        this.addTrail++;
      }
    } else {
      this.debrisTrail.shift();
      if (this.hitGround === 0) this.hitGround = 1;
      if (this.hitGround === 1) {
        for (
          let x = Math.round(this.x);
          x < this.x + Math.round(this.xSize);
          x++
        ) {
          game.groundHeight[x] = game.groundHeight[x] - this.ySize / 3;
          this.hitGround = 2;
        }
      }
    }
    if (this.debrisTrail.length > 4 || this.hitGround > 0) {
      this.debrisTrail.shift();
    }
  }
  draw(ctx) {
    let fallTime = 0;
    for (let x = 1; x < this.debrisTrail.length; x++) {
      ctx.fillStyle = `rgba(265,165,0,${0 + x / 5}`;
      ctx.lineWidth = 2;
      ctx.beginPath();
      ctx.fillRect(
        this.debrisTrail[x][0] - this.xSize / 2,
        this.debrisTrail[x][1] - this.ySize / 2,
        this.xSize * ((300 - fallTime * 5) / 300),
        this.ySize * ((300 - fallTime * 5) / 300)
      );
      ctx.fill();
      ctx.stroke();
      ctx.closePath();
      fallTime++;
    }

    ctx.fillStyle = `rgba(${255 - this.y / 2.5},${
      165 + (this.color - 1) * 33 - this.y / 2.9
    },0,1)`;
    ctx.fillRect(this.x, this.y, this.xSize, this.ySize);
  }
}
