export default class Tutorial {
  constructor(game) {
    this.data = game.getInfo();
    this.tutorialText = function (data) {
      return {
        movement: {
          text: `use <i class="fa fa-caret-square-o-left" style="font-size:36px"></i> and <i class="fa fa-caret-square-o-right" style="font-size:36px"></i> to move`,
          displayed: false
        },
        mousemovement: {
          text: `you can also move with <i class="fa-solid fa-computer-mouse"></i></i>`,
          displayed: false
        },
        mobilemovement: {
          text: `on mobile use your <i class="fa fa-hand-o-up" style="font-size:36px"></i> to move`,
          displayed: false
        },
        levels: {
          text: `for every 4 score you get a new row of bricks`,
          displayed: false
        },
        noob: {
          text: `best way to get points is to not die and destroy every brick`,
          displayed: false
        },
        rows: {
          text: `on every 2 rows bricks get tougher to destroy`,
          displayed: false
        },

        pro: {
          text: `try turning the music up`,
          displayed: false
        }
      };
    };
    this.specialText = function (data) {
      return {
        bugs: {
          text: `there are no bugs, only features`,
          displayed: false
        },
        speed: {
          text: `max speed you have achieved with your balls is ${data.maxSpeed} `,
          displayed: false
        },
        deaths: {
          text: `you have died ${data.deaths} time${
            data.deaths > 1 ? "s" : data.deaths > 10 ? "s that's alot" : ""
          }!`,
          displayed: false
        },
        debris: {
          text: `you have littered the streets with ${data.debrisGenerated} pieces of thrash!`,
          displayed: false
        },
        bit: {
          text: `we have mined ${
            data.debrisGenerated * data.deaths * data.maxSpeed
          } poopcoins on your computer while you were playing!`,
          displayed: false
        },
        music: {
          text: `music is Power Glove - Motorcycle Cop`,
          displayed: false
        },
        stage: {
          text: `highest stage you have reached is ${data.stage - 1}`,
          displayed: false
        }
      };
    };
    this.read = 0;
    this.getTexts = Object.values(this.tutorialText(this.data));
  }
  getTutorialText() {
    if (this.getTutorialStatus()) return "";
    for (let x = 0; x < this.getTexts.length; x++) {
      if (!this.getTexts[x].displayed) {
        this.getTexts[x].displayed = true;
        this.read++;
        return this.getTexts[x].text;
      }
    }
  }
  getData() {
    return this.data;
  }
  updateData(data) {
    this.data = data;
  }
  getSpecialText() {
    this.getSpecial = Object.values(this.specialText(this.data));
    let l = this.getSpecial.length;
    return this.getSpecial[Math.floor(Math.random() * l)].text;
  }
  getTutorialStatus() {
    return this.read === this.getTexts.length;
  }
}
