export default class Xplosion {
  constructor(x, y, delay) {
    this.x = x;
    this.y = y;
    this.color = "rgba(255,165,0,0.4)";
    this.evol = 0;
    this.delay = delay;
    this.dt;
  }
  draw(ctx) {
    if (this.delay <= 0) {
      if (this.evol < 100) {
        ctx.beginPath();
        ctx.fillStyle = `rgba(${
          this.evol < 50
            ? 255 - this.evol
            : this.evol < 60
            ? 200 - this.evol * 1.1
            : 100 - this.evol
        },${
          this.evol < 20
            ? 255 - this.evol
            : this.evol < 60
            ? 165 - this.evol * 1.1
            : 100 - this.evol
        },${
          this.evol < 10
            ? 255 - this.evol
            : this.evol < 60
            ? 0
            : 100 - this.evol
        },${this.evol < 5 ? 1 : 0.25})`;
        ctx.lineWidth = 0;
        ctx.arc(this.x, this.y, this.evol, 0, 2 * Math.PI);
        this.evol += this.dt;
        ctx.fill();
        ctx.closePath();
      }
    } else {
      this.delay--;
    }
  }
  update(game, element, time) {
    this.dt = 1 * Math.round(Math.abs(time / 8));
    if (this.evol > 100) {
      game.xploArray.splice(element, 1);
    }
  }
}
